<template>
  <section>
    <div class="cs-top">
      <em>  <br>  </em> 
      <div class="links">
        <a href="https://goto.kakao.com/@" class="kakao" target="_blank"> </a>
        <a @click="sendEmail('00', 'finset@koscom.co.kr')" class="email"> </a>
      </div>
    </div>

    <div class="cs-links">
      <p><a @click="$router.push('/etc/faqList')">  </a></p>
      <p><a @click="$router.push('/etc/term')">  </a></p>
      <p>
        <em></em>
        <em>{{app_version}}</em>
      </p>
      <p>
        <em> </em>
        <em>finset.@koscom.co.kr</em>
      </p>
    </div>

  </section>
</template>

<script>
import Constant from "./../../assets/js/constant.js";

export default {
  name: "EtcFaqMain",
  data() {
    return {
      app_version: "1.1"
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    if (Constant.userAgent == "Android") {
      this.app_version = window.Android.checkAppVersion();
      //checkAppVersion(this.app_version);
    } else if (Constant.userAgent == "iOS") {
      //  
      Jockey.on("receiveAppVersion", function(param) {
        this.app_version = param.appVersion;
        // checkAppVersion(param.appVersion);
      });
      //   
      Jockey.send("checkAppVersion");
    }
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    sendEmail: function(code, addr) {
      if (Constant.userAgent == "iOS") {
        Jockey.send("sendEmail", {
          code: code,
          addr: addr
        });
      } else if (Constant.userAgent == "Android") {
        window.Android.sendEmail(code, addr);
      }
    },
    //  ** 
    updateApp: function() {
      if (Constant.userAgent == "Android") {
        window.Android.updateApp();
      } else if (Constant.userAgent == "iOS") {
        Jockey.send("updateApp", {
          app_id: ""
        });
      }
    },
    /**
     * NATIVE CALL BACK
     */
    checkAppVersion: function(appVersion) {
      $("#app_version").html(appVersion);

      var version = "${newest_version}";
      var versionSplit = version.split("."); //
      var chkVersionSplit = appVersion.split("."); //
      // for (var i = 0; i < versionSplit.length; i++) {
      //   if (Number(versionSplit[i]) > Number(chkVersionSplit[i])) {
      //     $("#update").show();
      //     break;
      //   }
      // }
    }
  }
};
</script>

